
// Min side
// ---------------------------------------------------

// Representation lists
// ---------------------------------------

.representation_list {
	padding-left: 0;
	
	li {
		position: relative;
		@include rem('font-size', 20px);
		line-height: ( 28px / 20px );
		list-style: none;
		@include rem('border-bottom', 1px solid $janna);
		
		a {
			display: block;
			@include rem('padding', 19px 0 15px 34px);
			@include rem('min-height', 28px);
			
			.grunt-icon {
				position: absolute;
				@include rem('top', 17px);
				@include rem('left', -3px);
			}
		}
	}
}

.no_access_representation_list {
	padding-left: 0;
	
	li {
		@include rem('padding', 11px 0 9px);
		list-style: none;
		@include rem('border-bottom', 1px solid $janna);
	}
}

// Element lists
// ---------------------------------------

.element_list {
	padding-left: 0;
	
	li {
		position: relative;
		@include rem('min-height', 21px);
		@include rem('padding', 13px 0);
		list-style: none;
		@include rem('border-bottom', 1px solid $janna);
		@include clearfix();
	}

	.title_cell {
		display: block;
	}

	a.title_cell,
	.title_cell a {
		text-decoration: underline;
	}
    
    .meta_info_container {
        display: block;
        
        .meta_info_cell {
          display: inline-block;
          @include rem('min-width', 80px);
          @include rem('margin-right', 10px);
        }
    }
    
    // Are you sure to delete this?
    .are_you_sure {
        position: absolute;
        color: $white;
        text-align: right;
        @include calc(width, "100% - 4.25rem");
        @include rem('top', 45px);
        @include rem('right', 70px);
        @include rem('min-height', 21px);
        @include rem('padding', 3px 12px 5px);
        background-color: $brand_danger;
        @include border-radius(3px 0 0 3px);
        z-index: 2;
        @include box-sizing();
        
        span {
            @include rem('margin-right', 6px);
        }
        
        a {
            color: $white;
            text-decoration: underline;
            padding: 0;
            background-image: none;
        }

        &:after {
            position: absolute;
            top: 0;
            @include rem('right', -18px);
            content: "";
            @include rem('border-top', 18px solid transparent);
            @include rem('border-left', 18px solid $brand_danger);
            @include rem('border-bottom', 18px solid transparent);
        }
        
        @media (min-width: 400px) {
            width: auto;
        }
        @media (min-width: $md_break) {
            @include rem('top', 9px);
        }
    }
	
	// Mobile only
	@media (max-width: $md_break_max) {
		.title_cell {
			@include rem('margin-bottom', 7px);
		}
		.last_cell_right {
			float: right;
            text-align: right;
		}
	}
	
	// Tablet & desktop
	@media (min-width: $md_break) {

		.title_cell {
			display: inline-block;
		}

		.last_cell {
			position: absolute;
            right: 0;
			@include rem('top', 13px);
		}
        
        .meta_info_container {
            float: right;
            @include flexbox();
            @include flex-wrap($value: wrap);
            @include rem('margin-right', -10px);
            
            .meta_info_cell {
                text-align: right;
                
                &.gnr_cell {
                    order: 3;
                }
                &.bnr_cell {
                    order: 4;
                }
                &.fnr_cell {
                    order: 1;
                }
                &.snr_cell {
                    order: 2;
                }
            }
        }
	}
}

.form_date {
}

.delete_form {
	color: $brand_danger;

	&:hover,
	&:active {
		color: $brand_danger;
	}
}




// Case list
// ---------------------------------------
.case_list {
    padding-left: 0;
    
    li {
        list-style-image: none;
        @include rem('border', 1px solid $zombie);
        @include border-radius();
        @include rem('margin-bottom', 18px);
        @include clearfix();
    }
    .case_title {
        margin: 0;
        @include rem('font-size', 19px);
        line-height: ( 28px / 19px );
        @include SourceSansProRegular();
        @include rem('border-bottom', 1px solid $zombie);
        
        a {
            display: block;
            position: relative;
            @include rem('padding', 9px 38px 9px 15px);
            background-color: $rum_swizzle;
            @include border-radius($base_border_radius $base_border_radius 0 0);

            .arrow-icon {
                @include single-arrow-icon($direction: 'right', $color: $zombie);
                position: absolute;
                @include rem('top', 16px);
                @include rem('right', 20px);
            }
        }
    }
    .case_id_container {
        display: inline-block;
        @include rem('font-size', 16px);
        line-height: ( 21px / 16px );
        @include rem('margin', 4px 5px 5px 15px);
    }
    .case_id.not_found {
        color: $emperor;
    }
    .case_date_container {
        float: right;
        display: inline-block;
        @include rem('font-size', 16px);
        line-height: ( 21px / 16px );
        @include rem('margin', 4px 11px 5px 5px);
    }
    
    @media (min-width: $md_break) {
        .case_title a:after {
            @include rem('right', 15px);
        }
        .case_date_container {
            @include rem('margin-right', 15px);
        }
    }
}

.no_cases_found {
    @include rem('margin', 0 0 56px);
}


// Case search
// ---------------------------
.case_search_container {
  @include rem('margin-bottom', 42px);
  @include rem('max-width', 400px);

  form {
    position: relative;
  }

  .search_input {
    width: 100%;
    @include rem('border', 1px solid $form_main_color);
    margin: 0;
    @include rem('font-size', 24px);
    line-height: ( 28px / 24px );
    @include rem('padding', 6px 58px 6px 10px);
  }

  button {
    position: absolute;
    @include rem('top', 1px);
    @include rem('right', 1px);
    @include rem('bottom', 1px);
    color: $text_color;
    padding: 0;
    margin: 0;
    border-radius: none;
    border: 0 none;
    @include rem('border-left', 1px solid $form_main_color);
    @include border-radius(0 3px 2px 0);
    background-color: $danube;
    @include rem('width', 48px);
    @include rem-box-shadow(0 1px 0 rgba($white, 32/100) inset, 0 -1px 0 rgba($link_color, 32/100) inset);
    @include transition(background-color);
    
    .icon-searchglass {
      position: absolute;
      overflow: hidden;
      @include rem('top', 6px);
      @include rem('left', 8px);
    }

    &:hover,
    &:active {
        background-color: $danube_hover;
    }
  }
}




// Ad box
// ---------------------------------------
.promo_box {
    position: relative;
    display: block;
    width: 100%;
    text-align: center;
    background-color: $white_sand;
    @include border-radius();
    @include rem('border', 1px solid $alto);
	@include rem('padding', 55px 21px 7px);
	@include rem('margin', 84px 0 56px);
    @include box-sizing();
    
    .grunt-icon {
        position: absolute;
        left: 0;
        right: 0;
        @include rem('top', -23px);
        margin: 0 auto;
        background-color: $white;
        @include border-radius(6px);
        @include rem('border', 1px solid $alto);
        @include box-sizing($boxmodel: content-box);
    }
    
    .promo_header {
        @include SourceSansProRegular();
        @include rem('font-size', 20px);
        line-height: ( 28px / 20px );
        @include rem('margin', 0 0 14px);
    }
    p {
        @include rem('margin-bottom', 14px);
    }
}



// Enkel sak
// ---------------------------------------
.case_shortcut_link_container {
    @include rem('margin-bottom', 56px);
}

.case_shortcut_link {
    @include rem('font-size', 20px);
    line-height: ( 28px / 20px );
    @include rem('padding-left', 25px);
    position: relative;
    
    .icon-arrow-left-bow {
        position: absolute;
        @include rem('top', -3px);
        @include rem('left', -4px);
    }
}


// Case documents
// ---------------------------------------
.case_document_container {
    @include rem('border', 1px solid $zombie);
    @include border-radius();
    @include rem('margin-bottom', 14px);
    
    .case_document_date {
        display: block;
        position: relative;
        @include rem('padding', 6px 12px 6px 36px);
        @include rem('font-size', 16px);
        line-height: ( 21px / 16px );
        background-color: $rum_swizzle;
        @include border-radius($base_border_radius $base_border_radius 0 0);
        @include rem('border-bottom', 1px solid $zombie);
    
        &:before {
            position: absolute;
            content: "";
            @include rem('top', 1px);
            @include rem('left', 6px);
            @include rem('height', 32px);
            @include rem('width', 32px);
        }
        
        &.sent {
            @include pseudo-icon($extend: ".icon-arrow-left-bow",
            $position: 'before');
        }
        
        &.received {
            @include pseudo-icon($extend: ".icon-arrow-right-bow",
            $position: 'before');
        }
    }
    
    .case_document_header {
        margin: 0;
        @include SourceSansProRegular();
        @include rem('font-size', 20px);
        line-height: ( 28px / 20px );
        @include rem('padding', 10px 14px);
        @include rem('border-bottom', 1px solid $zombie);
    }
    
    .case_document_list {
        padding-left: 0;
        margin-bottom: 0;
        
        li {
            list-style-image: none;
            @include rem('border-bottom', 1px solid $zombie);
        
            &:last-child {
                border-bottom: none;
                
                .case_document_link {
                    @include border-radius(0 0 $base_border_radius $base_border_radius);
                }
            }
        }
    
        .case_document_link {
            display: block;
            position: relative;
            @include rem('padding', 13px 16px 14px 43px);
            text-decoration: none;
            
            &:hover,
            &:active {
                background-color: lighten($rum_swizzle, 3);
            }
    
            .icon {
                position: absolute;
                text-indent: -3000em;
                overflow: hidden;
                @include rem('top', 12px);
                @include rem('left', 14px);
                @include rem('width', 18px);
                @include rem('height', 24px);
                background: url(../images/file.svg) no-repeat 0 0 transparent;
               
                &.doc,
                &.docx,  
                &.DOC,
                &.DOCX {
                    background-image: url(../images/doc.svg);
                }

                &.pdf,
                &.PDF {
                    background-image: url(../images/pdf.svg);
                }
            }
            
            .document_title {
                display: block;
                @include rem('font-size', 17px);
                line-height: ( 21px / 17px );
                text-decoration: underline;
            }
            
            .document_meta {
                color: $text_color;
            }
            
            .unseen {
                color: $brand_danger;
                @include rem('font-size', 17px);
                line-height: ( 21px / 17px );
            }
        }
    }
    
    @media (min-width: $md_break) {
        position: relative;
        z-index: 0;
        @include rem('margin-bottom', 21px);
        
        .case_document_date {
            position: absolute;
            z-index: 0;
            content: "";
            top: 0;
            bottom: 0;
            left: 0;
            @include rem('border-right', 1px solid $zombie);
            @include border-radius($base_border_radius 0 0 $base_border_radius);
            border-bottom: 0 none;
            @include box-sizing();
            @include rem('width', 114px);
            @include rem('padding-top', 14px);
            @include rem('padding-left', 33px);
            
            &:before {
                @include rem('top', 9px);
                @include rem('left', 3px);
            }
        }
        
        .case_document_header {
            position: relative;
            z-index: 1;
            @include rem('min-height', 21px);
            @include rem('padding', 10px 18px);
            @include rem('margin-left', 114px);
            
            &:before {
                position: absolute;
                content: "";
                @include rem('bottom', -1px);
                @include rem('left', -114px);
                @include rem('width', 114px);
                @include rem('border-bottom', 1px solid $zombie);
            }
        }
        
        .case_document_list {
            @include rem('margin-left', 114px);
            
            .case_document_link {
                @include rem('padding-left', 51px);
    
                .icon {
                    @include rem('left', 18px);
                }
                
                .unseen {
                    float: right;
                    position: relative;
                    @include rem('top', -20px);
                }
            }
        }
    }
}



// Eiendommer-siden
// ---------------------------------
.load_more_properties {
    text-align: center;
    @include rem('margin-bottom', 56px);
    
}
.properties_container + .load_more_properties {
    @include rem('margin-top', -35px);
}



// braArkiv-siden
// ---------------------------------

.braarkiv_case_list {
    padding: 0;
    @include rem('margin-bottom', 56px);
    
    li {
        list-style-image: none;
        @include rem('border-bottom', 1px solid $janna);
    }
    
    .braarkiv_document_link {
        display: block;
        position: relative;
        text-decoration: none;
        @include rem('min-height', 23px);
        @include rem('padding', 10px 90px 10px 30px);
    
        &:before {
            position: absolute;
            left: 0;
            content: "";
            @include rem('top', 12px);
            @include rem('width', 18px);
            @include rem('height', 24px);
            background: url(../images/pdf.svg) no-repeat 0 0 transparent;
        }

        &:hover,
        &:active {
            background-color: lighten($rum_swizzle, 3);
            
            .document_title {
                text-decoration: underline;
            }
        }
    }
    
    .unseen {
        display: block;
        @include rem('font-size', 16px);
        line-height: ( 21px / 16px );
        color: $brand_danger;
    }
    
    .date {
        position: absolute;
        right: 0;
        @include rem('top', 14px);
        @include rem('font-size', 16px);
        line-height: ( 21px / 16px );
        color: $emperor;
    }
    
    @media (min-width: $md_break) {
        .braarkiv_document_link {
            @include rem('padding-right', 180px);
        }
        .unseen {
            position: absolute;
            @include rem('right', 120px);
            @include rem('top', 14px);
        }
    }
}









