.linkbox {
    width: 100%;
    
    &__header {
        margin: 0;
        @include rem('font-size', 24px);
        line-height: (28px / 24px);
        @include SourceSansProRegular();
    }

    &__header-link {
        position: relative;
        background-color: $rum_swizzle;
        display: block;
        @include rem('border', 1px solid $almond);
        @include border-radius();
        @include rem('padding', 6px 12px 6px 26px);
        @include box-sizing();
        width: 100%;

        .arrow-icon {
            @include single-arrow-icon($direction: 'right', $size: 'small', $color: $link_color);
            position: absolute;
            @include rem('top', 15px);
            @include rem('left', 11px);
        }
        .arrow-icon--2 {
            @include single-arrow-icon($direction: 'right', $size: 'small', $color: $link_color);
            position: absolute;
            @include rem('top', 15px);
            @include rem('left', 17px);
        }
    }
    
    &__content {
        position: relative;
        z-index: 0;
        @include rem('padding', 5px 24px 0);
        @include rem('margin-bottom', 7px);
        
        a {
            text-decoration: underline;
        }

        ul {
            @include rem('padding-left', 15px);
        
            // Firefox align the list-items 3px wrong
            @-moz-document url-prefix() {
                @include rem('padding-left', 18px);
            }

            a {
                text-decoration: none;

                &:hover,
                &:active {
                    text-decoration: underline;
                }
            }
        }
        
        // Vertical distance of any element inside the content-box
        // ul, p, ++
        & > * {
            margin-bottom: 0;
        }
    }
    
    // Tablet & desktop
    @media only screen and (min-width: $md_break) {
        @include rem('padding-bottom', 9px);
        @include rem('border', 1px solid $almond);
        @include border-radius();
        
        &__header-link {
            border: 0 none;
            @include rem('border-bottom', 1px solid $almond);
            @include border-radius($base_border_radius $base_border_radius 0 0);
        }
    }
}


// Category link at the bottom // Forside
// ----------------------------------------------------------------
.linkbox--with-category-link .linkbox__category-link-container {
    @include rem('border-top', 1px solid $almond);
    text-align: right;
    @include rem('font-size', 17px);
    line-height: ( 21px / 17px );
    @include rem('margin', 0 24px);
    @include rem('padding', 6px 0 0);
}

// Tablet & desktop / Align link to bottom, regardless to the height of the box
@media only screen and (min-width: $md_break) {
    .equal_height_container .equal_height_item .linkbox--with-category-link {
        @include flexbox();
        @include flex-direction(column);

        .linkbox__category-link-container {
            margin-top: auto;
        }
    }
}
    


// Linkboks liten - mindre header, mindre avstand, uten lenkepiler
// ----------------------------------------------------------------
.linkbox--small {
    @include border-radius();
    @include rem('border', 1px solid $zombie);
    
    .linkbox__header {
        @include rem('font-size', 20px);
        line-height: (28px / 20px);
    }
    
    .linkbox__header-link {
        @include rem('padding-left', 14px);
        border: none;
        @include rem('border-bottom', 1px solid $zombie);
        @include border-radius($base_border_radius $base_border_radius 0 0);
    }
    
    .linkbox__content {
        @include rem('padding', 5px 14px 0);
    }
}

// Linkboks med ikon og inline lenke-liste for /tjenester
// -------------------------------------------------------
.linkbox--servicecat {
    position: relative;
    
    &__dragula-handle {
        position: absolute;
        @include rem('top', 1px);
        @include rem('right', 1px);
        z-index: 2;
        background-color: $rum_swizzle;
        @include border-radius();
        cursor: move;
        cursor: grab;
        cursor: -moz-grab;
        cursor: -webkit-grab;
        @include rem('padding', 3px 8px);
    }
    
    .linkbox__content {
        margin: 0;
        @include rem('padding', 7px 0 0);
    }
    
    .linkbox__icon-link {
        float: left;
        @include rem('margin-top', 2px);
        @include rem('margin-left', 2px);
    }
    
    .linkbox__cat_list {
        @include rem('min-height', 70px);
        @include rem('margin', 0 0 0 57px);

        li {
            display: inline;
            list-style-image: none;
        }

        li + li:before {
            content: ", ";
        }
    }
    
    // Tablet & desktop
    @media only screen and (min-width: $md_break) {
        padding-bottom: 0;
        
        // divider between icon and shortcuts
        &:after {
            position: absolute;
            z-index: 0;
            top: 0;
            display: block;
            content: "";
            background-color: $almond;
            height: 100%;
            @include rem('width', 1px);
            @include rem('left', 88px);
        }
        
        .linkbox__header {
            position: relative;
            z-index: 1;
        }
        
        .linkbox__icon-link {
            @include rem('margin-left', 12px);
        }
        
        .linkbox__cat_list {
            @include rem('min-height', 77px);
            @include rem('margin-left', 90px);
            @include rem('padding-right', 12px);
            @include rem('padding-bottom', 14px);
        }
    }
}

// Dragula - drag and drop effect
// -------------------------------------------------------------
.linkbox-dragula-container {
    // in-flight clone
    &.gu-mirror {
        position: fixed !important;
        // margin: 0 !important;
        z-index: 9999 !important;
        pointer-events: none; // Prevent all click, state and cursor options
        list-style: none;
        opacity: .8;
        -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=80)";
        filter: alpha(opacity=80);
        
        .linkbox {
            @include outline();
            background-color: $white;
        }
    }

    // high-performance display:none; helper
    .gu-hide {
        left: -9999px !important;
    }

    // added to mirrorContainer (default = body) while dragging
    .gu-unselectable {
        -webkit-user-select: none !important;
        -moz-user-select: none !important;
        -ms-user-select: none !important;
        user-select: none !important;
    }

    // added to the source element while its mirror is dragged
    &.gu-transit .linkbox--servicecat {
        position: relative;
        border-style: dashed;
        
        .linkbox--servicecat__dragula-handle,
        .linkbox__header-link,
        .linkbox__content,
        &:after {
            display: none;
        }
        
        &:before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            @include border-radius(3px 3px 0 0);
            @include rem('height', 40px);
            background-color: $rum_swizzle;
            @include rem('border-bottom', 1px dashed $almond);
        }
    }
}



