// Main header navigation
// ---------------------------

// Menu button
.open_mobile_main_nav {
    position: absolute;
    @include rem('left', 10px);
    @include rem('top', 22px);
    color: $black;
    background-color: $rum_swizzle;
    @include rem('border', 1px solid $zombie);
    text-transform: uppercase;
    @include rem('font-size', 10px);
    line-height: ( 14px / 10px );
    text-align: center;
    @include rem('padding', 3px 5px 5px);
    @include border-radius();
    @include transition(background-color);

    &:hover {
        background-color: darken($rum_swizzle, 10);
        text-decoration: none;
    }

    .icon-nav_toggle {
        display: block;
        @include rem('height', 16px);
        @include rem('width', 32px);
        background-position: center;
    }

    @media (min-width: $md_break) {
        display: none;
    }
}

// Mobile only
@media (max-width: $md_break_max) {
  
    .main_nav {
        .nav_container {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            z-index: 100;
            width: 88%;
            color: $white;
            background-color: $nav_mobile_bg;
            @include rem-box-shadow(2px 0 4px rgba($black, 40/100));
            @include transition($property: margin-left, $duration: 200ms, $timing-function: ease);

            a {
                color: $white;
            }
        }
      
        &.main_nav_collapsed .nav_container {
            margin-left: -115%;

            a {
                display: none;
            }
        }
    
        .close_mobile_main_nav {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 88%;
            z-index: 100;
            width: 12%;
            background-color: rgba($black, 48/100);
            @include transition($property: margin-left, $duration: 200ms, $timing-function: ease);
            
            .close_mobile_main_nav_link {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                outline: 0 none;
                
                .close_mobile_main_nav_button {
                    position: absolute;
                    left: 0;
                    @include rem('top', 52px);
                    @include rem('width', 38px);
                    @include rem('height', 38px);
                    background-color: $nav_mobile_bg;
                    @include border-radius(0 $base_border_radius $base_border_radius 0);
                    @include rem-box-shadow(3px 0 4px rgba($black, 24/100));
                    @include box-sizing($boxmodel: content-box);

                    .icon-close {
                        @include rem('margin', 3px 0 0 3px);
                    }
                }

                &:focus .close_mobile_main_nav_button {
                    @include outline();
                }
            }
        }
      
        &.main_nav_collapsed .close_mobile_main_nav {
            margin-left: -115%;

            a {
                display: none;
            }
        }
    }
  
    // Main navigation list // mobile
    .main_nav_list {
        padding: 0;
        @include rem('margin', 42px 40px 25px 0);

        li {
            list-style-image: none;
            @include rem('border-bottom', 1px solid $thunderbird);
        }
        a {
            display: block;
            @include rem('padding', 10px 0 10px 20px);
            @include rem('font-size', 21px);
            line-height: ( 28px / 21px );
        }
        li.active a {
            @include rem('padding-left', 36px);
            background: url("../images/white_list_style_image.svg") no-repeat 21px 21px transparent;
        }
    }

    // Secondary navigation list // mobile
    .secondary_nav {
        padding: 0;
        @include rem('margin', 0 40px 30px 0);

        a {
            position: relative;
            display: inline-block;
            @include rem('padding', 7px 25px);
            @include rem('margin-left', 20px);

            .grunt-icon {
                position: absolute;
                @include rem('top', 5px);
                @include rem('left', -8px);
            }
        }
    }
}

// Prevent sliding when changing size from desktop to mobile
@media (min-width: 650px) and (max-width: 700px) {
    .main_nav.main_nav_collapsed .nav_container,
    .main_nav.main_nav_collapsed .close_mobile_main_nav {
        -webkit-transition: none !important;
        -moz-transition: none !important;
        -ms-transition: none !important;
        -o-transition: none !important;
        transition: none !important;
    }
}

// Tablet and desktop
@media (min-width: $md_break) {

    .main_nav {
  
        // Don't show close button 
        .close_mobile_main_nav {
            display: none;
        }

        // Main navigation list // tablet
        .main_nav_list {
            background-color: $white_sand;
            padding: 0;
            text-align: center;
            @include rem('margin', 8px 0 0);
            @include rem('border', 1px solid $alto);
            border-bottom: 0 none;
            @include rem-box-shadow(0 1px 1px $white inset);
            @include border-radius($base_border_radius $base_border_radius 0 0);

            // Flexbox
            @include flexbox();
            @include flex-wrap($value: nowrap);
            // @include justify-content($value: center); // flex-start / flex-end / space-between / space-around / center

            li {
                display: inline-block;
                list-style-image: none;
                text-align: center;
                @include flex($fg: 1, $fs: 0, $fb: auto);
                // $fg = flex-grow    = give each item 1 share of extra width
                // $fs = flex-shrink  = don’t let items shrink at all
                // $fb = flex-basis   = start items at 200 pixels wide
            }
            a {
                display: block;
                color: $black;
                @include rem('font-size', 20px);
                line-height: ( 28px / 20px );
                @include rem('padding', 6px 10px 4px);
            }
            li.active {
                @include rem('margin-top', -3px);

                a {
                    position: relative;
                    color: $white;
                    @include rem('padding-top', 9px);
                    text-shadow: 0 1px 2px #a31d22;
                    @include border-radius($base_border_radius $base_border_radius 0 0);
                    background: $main_red;
                    background: linear-gradient(#f53137, $main_red);
                }
            }
        }

        // Secondary navigation list // desktop
        .secondary_nav {
            display: none;
        }
    }
}

// iPad font-fix for the active element
@media only screen 
    and (min-device-width: 768px) 
    and (max-device-width: 1024px) 
    and (-webkit-min-device-pixel-ratio: 1) {
        nav .main_nav_list li.active a {
            @include SourceSansProSemibold();
        }
}

// Desktop only
@media (min-width: $lg_break) {

    // Main navigation list // desktop
    .main_nav .main_nav_list {
        background-color: transparent;
        border: 0 none;
        box-shadow: inherit;
        @include border-radius(0);

        li {
            @include flex($fg: 1, $fs: 0, $fb: 19%);
            @include rem('margin', 0 3px 0 2px);
            background-color: $white_sand;
            @include rem('border', 1px solid $alto);
            border-bottom: 0 none;
            @include rem-box-shadow(0 1px 1px $white inset);
            @include border-radius($base_border_radius $base_border_radius 0 0);

            &:first-child {
                margin-left: 0;
            }
            &:last-child {
                margin-right: 0;
            }
        }
        a {
            @include rem('font-size', 21px);
            line-height: ( 28px / 21px );
            @include rem('padding-bottom', 6px);
        }
        li.active {
            border: 0 none;

            a {
                @include rem('padding-top', 10px);
                @include rem('padding-bottom', 7px);
            }
        }
    }
}


// Logout link // inside main navigation container
// ----------------------------------------------------
.mobile_logout_link {
    display: none;
    position: relative;
	@include rem('margin', 0 40px 30px 0);
    @include rem('padding', 10px 0 10px 45px);
    @include rem('font-size', 21px);
    line-height: ( 28px / 21px );
    @include rem('border-top', 1px solid $thunderbird);
    @include rem('border-bottom', 1px solid $thunderbird);
    
    .icon-logout {
        position: absolute;
        @include rem('top', 8px);
        @include rem('left', 13px);
        @include rotate(180);
    }
    
    @media (min-width: $md_break) {
        display: none !important;
    }
}

.is_logged_in .mobile_logout_link {
    display: block;
}