// Årsoversikt
// ---------------------------------

.select_year_list_container {
    position: relative;
    width: 100%;
    @include rem('height', 69px);
    @include rem('padding-bottom', 14px);
    
    white-space: nowrap;
    overflow-x: scroll; // has to be scroll, not auto
    overflow-y: hidden;
    -webkit-overflow-scrolling: touch;
    // -ms-overflow-style: -ms-autohiding-scrollbar;
    -ms-overflow-style: none;
    
    &::-webkit-scrollbar {
        display: none !important;
        width: 0 !important;
        height: 0 !important;
    }
    
    // FF
    overflow: -moz-scrollbars-none;
}

.select_year_list {
    position: absolute;
    left: 0;
    top: 0;
    padding: 0;
    margin: 0;
    @include clearfix();
    
    &.year_count_2 { @include rem('width', 2 * 84px + 4px); }
    &.year_count_3 { @include rem('width', 3 * 84px + 4px); }
    &.year_count_4 { @include rem('width', 4 * 84px + 4px); }
    &.year_count_5 { @include rem('width', 5 * 84px + 4px); }
    &.year_count_6 { @include rem('width', 6 * 84px + 4px); }
    &.year_count_7 { @include rem('width', 7 * 84px + 4px); }
    &.year_count_8 { @include rem('width', 8 * 84px + 4px); }
    &.year_count_9 { @include rem('width', 9 * 84px + 4px); }
    &.year_count_10 { @include rem('width', 10 * 84px + 4px); }
    &.year_count_11 { @include rem('width', 11 * 84px + 4px); }
    &.year_count_12 { @include rem('width', 12 * 84px + 4px); }
    &.year_count_13 { @include rem('width', 13 * 84px + 4px); }
    &.year_count_14 { @include rem('width', 14 * 84px + 4px); }
    &.year_count_15 { @include rem('width', 15 * 84px + 4px); }
    &.year_count_16 { @include rem('width', 16 * 84px + 4px); }
    &.year_count_17 { @include rem('width', 17 * 84px + 4px); }
    &.year_count_18 { @include rem('width', 18 * 84px + 4px); }
    &.year_count_19 { @include rem('width', 19 * 84px + 4px); }
    &.year_count_20 { @include rem('width', 20 * 84px + 4px); }
    &.year_count_21 { @include rem('width', 21 * 84px + 4px); }
    &.year_count_22 { @include rem('width', 22 * 84px + 4px); }
    &.year_count_23 { @include rem('width', 23 * 84px + 4px); }
    &.year_count_24 { @include rem('width', 24 * 84px + 4px); }
    &.year_count_25 { @include rem('width', 25 * 84px + 4px); }

    li {
        position: relative;
        list-style-image: none;
        display: inline-block;
        float: left;
        
        a {
            position: relative;
            display: block;
            text-align: center;
            @include rem('width', 84px);
            @include rem('padding', 13px 0);
            background-color: lighten($rum_swizzle, 3);
            @include rem('border-top', 1px solid $zombie);
            @include rem('border-bottom', 1px solid $zombie);
            
            &:before {
                content: "";
                position: absolute;
                @include rem('left', -1px);
                @include rem('top', 6px);
                @include rem('bottom', 6px);
                @include rem('width', 1px);
                background-color: $janna;
            }
            
            &:focus {
                outline: none;
            }
        }
        
        &:first-child a {
            @include rem('border-left', 1px solid $zombie);
            @include border-radius($base_border_radius 0 0 $base_border_radius);
        }
        
        &:last-child a {
            @include rem('border-right', 1px solid $zombie);
            @include border-radius(0 $base_border_radius $base_border_radius 0);
        }
        
        &:first-child a:before,
        &:last-child a:after {
            display: none;
        }
        
        // Red line
        &.active:before {
            content: "";
            z-index: 2;
            position: absolute;
            bottom: 0;
            width: auto;
            @include rem('left', 22px);
            @include rem('right', 22px);
            @include rem('height', 2px);
            background-color: $brand_danger;
        }
        
        // Red arrow
        &.active:after {
            content: "";
            z-index: 2;
            position: absolute;
            width: auto;
            top: 100%;
            left: 50%;
            border: solid transparent;
            height: 0;
            width: 0;
            border-top-color: $brand_danger;
            @include rem('border-width', 6px);
            @include rem('margin-left', -6px);
        }
        
        &.active a {
            color: $text_color;
            background-color: $rum_swizzle;
            @include SourceSansProRegular();

            &:focus,
            &:hover,
            &:active {
                text-decoration: none;
            }

            &:before {
                content: "";
                z-index: 1;
                position: absolute;
                top: 0;
                bottom: 0;
                @include rem('left', -1px);
                @include rem('width', 1px);
                background-color: $zombie;
            }

            &:after {
                content: "";
                z-index: 1;
                position: absolute;
                top: 0;
                bottom: 0;
                @include rem('right', -1px);
                @include rem('width', 1px);
                background-color: $zombie;
            }
        }
    }
}


.select_year_gradient_container {
    position: relative;
    @include rem('margin-bottom', 28px);
    
    &:before,
    &:after {
        display: none;
        position: absolute;
        top: 0;
        z-index: 2;
        content: "";
        @include rem('width', 40px);
        @include rem('height', 56px);
    }

    &:before {
        left: 0;
        background: transparent;
        background: linear-gradient(to left, rgba($white, 0) 30%, $white 70%);
    }

    &:after {
        right: 0;
        background: transparent;
        background: linear-gradient(to right, rgba($white, 0) 30%, $white 70%);
    }

    .scroll-to-left-link,
    .scroll-to-right-link {
        display: none;
        cursor: pointer;
        position: absolute;
        top: 0;
        @include rem('width', 40px);
        @include rem('height', 56px);
        z-index: 5;
    }
    .scroll-to-left-link {
        left: 0;

        .arrow-icon {
            @include single-arrow-icon($direction: 'left', $size: 'large');
            position: absolute;
            right: inherit;
            @include rem('top', 18px);
            @include rem('left', 14px);
        }
    }
    .scroll-to-right-link {
        right: 0;

        .arrow-icon {
            @include single-arrow-icon($direction: 'right', $size: 'large');
            position: absolute;
            @include rem('top', 18px);
            @include rem('right', 14px);
        }
    }

    // Active
    &.js_scrollToLeftGradient:before,
    &.js_scrollToRightGradient:after {
        display: block;
    }

    &.js_scrollToLeftGradient .scroll-to-left-link,
    &.js_scrollToRightGradient .scroll-to-right-link {
        display: block;
    }
}


@media (min-width: $md_break) {
    .select_year_gradient_container {
        &:before,
        &:after {
            @include rem('height', 42px);
            @include rem('width', 60px);
        }
        .scroll-to-left-link,
        .scroll-to-right-link {
            @include rem('height', 42px);
        }
        .scroll-to-left-link .arrow-icon {
            @include rem('top', 12px);
            @include rem('left', 7px);
        }
        .scroll-to-right-link .arrow-icon {
            @include rem('top', 12px);
            @include rem('right', 7px);
        }
    }
    .select_year_list_container {
        @include rem('height', 56px);
    }
    .select_year_list li a {
        @include rem('padding', 6px 0 7px);
    }
}


// Fakturaer tabell - gammel oppsett
// ---------------------------------------------
.invoice_table {
    
    .invoice_table_caption {
        height: 0;
        opacity: 0;
    }
    
    .div_table_row {
        @include box-sizing();
        @include clearfix();
        @include rem('padding-bottom', 6px);
        @include rem('margin-bottom', 7px);
        @include rem('border-bottom', 1px solid $janna);        
    }
    
    .div_table_cell {
        float: left;
        @include rem('min-height', 1px);
        @include box-sizing();
    }
    
    .div_table_row_title {
        width: 100%;
    }
    
    .div_table_row_content {
        width: 33.33333333%;
        @include rem('padding-right', 10px);
        
        &:last-child {
            padding-right: 0;
        }
    }
    
    .div_table_mobile_header {
        color: $emperor;
        display: block;
        @include rem('font-size', 16px);
        line-height: ( 21px / 16px );
    }
    
    .div_table_desktop_header_row {
        display: none;
    }
    
    @media only screen and (min-width: $md_break) {        
        
        .div_table_row_title {
            width: 55%;
            @include rem('padding-right', 10px);
        }
        
        .div_table_row_content {
            width: 15%;
        }
        
        .div_table_mobile_header {
            display: none;
        }        
    
        .div_table_desktop_header_row {
            color: $emperor;
            display: block;
            @include box-sizing();
            @include clearfix();
            @include rem('font-size', 16px);
            line-height: ( 21px / 16px );
            @include rem('margin-bottom', 4px);
            @include rem('padding-bottom', 2px);
            @include rem('border-bottom', 1px solid $zombie);
        }
    
        .div_table_row {
            @include rem('padding-bottom', 3px);
            @include rem('margin-bottom', 3px);  
        
            .div_table_row_content {
                @include rem('padding-top', 7px);
            }     
        }
    }
}


// Faktura tabell - Tittel
// -------------------------------------
.invoice_link {
    display: block;
    position: relative;
    @include rem('margin-left', -3px);
    @include rem('padding', 7px 0 7px 32px);
    text-decoration: underline;
    
    .file_icon {
        display: block;
        position: absolute;
        @include rem('width', 32px);
        @include rem('height', 32px);
        @include rem('top', 9px);
        @include rem('left', 3px);
        background: url(../images/file.svg) no-repeat 0 0 transparent;        
        
        &.papirfaktura {
            background-image: url(../images/pdf.svg);
        }        
        &.doc-faktura {
            background-image: url(../images/doc.svg);
        }        
        &.e-faktura {
            background-image: url(../images/html.svg);
        }
    }
    &--disabled {
        text-decoration: none;
        color: $emperor;
    }
}